var that;
export default {
	data() {
		return {
			index: 0,
			date: '',
			info: '',
			sex: [{ id: 1, name: '男' }, { id: 2, name: '女' }]
		};
	},
	methods: {
		getinput(e) {
			let name = e.currentTarget.dataset.name;
			that.info[name] = e.detail.value;
		},
		getuserInfo() {
			that.$zax.userinfo().then(res => {
				that.info = res.user_info;
				console.log(res);
			});
		},
		bindPickerChange(e) {
			that.index = e.detail.value;
		},
		bindDateChange(e) {
			that.date = e.detail.value;
		},
		formSubmit(e) {
			if (that.info.binding && !that.$form.isTel(that.info.binding)) {
				that.$alert('电话号码有误');
				return;
			}
			that.$request
				.request({
					url: that.api.user.UpdateUser,
					data: {
						nickname: that.info.nickname,
						binding: that.info.binding,
						birthday: that.date,
						sex: that.sex[that.index].id
					},
					method: 'post'
				})
				.then(res => {
					that.$alertok('修改成功！');
					that.$zax.userinfo();
				});
		}
	},
	onLoad() {
		that = this;
		that.info = uni.getStorageSync('userinfo');
		if (that.info.sex == 1) {
			that.index = 0;
		} else {
			that.index = 1;
		}
		console.log(that.index);
		that.$forceUpdate();
		that.date = that.info.birthday;
		that.getuserInfo();
	}
};
